import { css } from '@emotion/react';
import { useRouter } from 'next/router';

import { Typography } from 'src/components/dataDisplay';
import { FullWidthBox, Stack } from 'src/components/layout';
import { Paper } from 'src/components/surface';
import useProductInfo from 'src/hooks/useProductInfo';
import useSurveysForBestFit from 'src/hooks/useSurveysForBestFit';

import { BASE_PATH } from '../../constants/resouce';

const paperLayout = css`
  display: flex;
  align-items: center;
  justify-content: center;

  text-align: center;
  color: #ffffff;

  padding: 0px 20px;
`;

const fullWidth = css`
  flex: 1;
`;
const optionLayout = css`
  height: 160px;
  padding-top: 20px;
  padding-bottom: 20px;
  border: 1px solid #ffffff;
  border-radius: 8px;
  color: #ffffff;
  background: transparent;

  width: 100%;

  overflow: hidden;
  word-break: keep-all;
  overflow-wrap: break-word;
`;

const removeFirstSlash = (path: string) => {
  if (!path || !path.length) {
    return path;
  }
  const first = path.substring(0, 1);
  if (first !== '/') {
    return path;
  }
  return path.substring(1);
};

interface BestFitProductPaperProps {
  index: number;
}

const BestFitProductQuestionPaper = ({
  index,
}: BestFitProductPaperProps): JSX.Element | null => {
  const router = useRouter();
  const { pathname = '', basePath = '' } = router;
  const { name: brandName } = useProductInfo();
  const { surveyBackgroundImage, surveys } = useSurveysForBestFit({
    brandName,
  });
  if (!surveys || index < 0 || index > surveys.length) {
    return null;
  }
  const { question, options } = surveys[index];
  const questions = question.split('\n');
  const handleClick = () => {
    const next = document.getElementById(`question-${index + 1}`);
    if (next) {
      next.scrollIntoView();
    } else {
      const pathnames = pathname.split('/');
      pathnames.pop(); // Remove the last element
      const parentPathname = removeFirstSlash(pathnames.join('/'));
      router.push(`${BASE_PATH}${basePath}${parentPathname}/result?id=${1}`);
    }
  };
  return (
    <FullWidthBox>
      <div id={`question-${index}`} />
      <Paper backgroundUrl={surveyBackgroundImage} css={paperLayout}>
        <Stack spacing={40} justifyContent="center" css={fullWidth}>
          <Typography variant="h3">
            {questions.map((text, index) => {
              const key = `${index}-${text}`;
              return <div key={key}>{text}</div>;
            })}
          </Typography>
          <Stack direction="row" spacing={20} justifyContent="space-between">
            {options.map((option, index) => {
              const key = `${index}-${option}`;
              const texts = option.split('\n');
              return (
                <button
                  key={key}
                  css={optionLayout}
                  type="button"
                  onClick={handleClick}
                >
                  {texts.map((text, lineIndex) => {
                    const lineKey = `${lineIndex}-${text}`;
                    return (
                      <span key={lineKey}>
                        {text}
                        <br />
                      </span>
                    );
                  })}
                </button>
              );
            })}
          </Stack>
        </Stack>
      </Paper>
    </FullWidthBox>
  );
};

export default BestFitProductQuestionPaper;
